body {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Nanum Gothic Coding", monospace;
  background-image: url(https://i.imgur.com/Xp7dFUB.png);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  background-color: black;
}

.Todo {
  margin: 10px;
}

.Todo-buttons {
  margin-top: 10px;
}

h1 {
  color: #00c00a;
  font-size: 3rem;
}

/* Todo List - main Div */

.TodoList {
  position: relative;
  top: -19px;
  height: 52.2vh;
  width: 370px;
  border: 2px solid rgb(59, 59, 59);
  background-color: rgb(0, 0, 0);
}

.Todo-screen {
  transition: 2s;
  opacity: 1;
}

.Todo-h3 {
  font-weight: 600;
  font-size: 1.2rem;
  display: inline;
  color: #00c00a;
  width: 100px;
  height: 25px;
  overflow: auto;
  transition: 1s;
}

.Todo-Not-Active {
  text-decoration: line-through;
}

input {
  border: 2px solid white;
  -webkit-box-shadow: inset 0 0 8px rgba(253, 253, 253, 0.144),
    0 0 5px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: inset 0 0 8px rgba(0, 0, 0, 0.1), 0 0 16px rgba(0, 0, 0, 0.1);
  box-shadow: inset 0 0 8px rgba(0, 0, 0, 0.1), 0 0 16px rgba(0, 0, 0, 0.1);
  padding: 5px;
  width: 150px;
  /* background: rgba(255, 255, 255, 0.5); */
  margin: 0 0 10px 0;
  outline: none;
}

.NewTodoForm {
  position: relative;
  top: -50px;
  left: 30px;
}

button {
  font-size: 11px;
  outline: 1px solid #000000;
  background: #c0c0c0;
  border-width: 1px;
  border-style: solid;
  border-color: #ffffff #808080 #808080;
}

.NewTodoForm button {
  position: relative;
  left: -20px;
}

.TodoList-todos {
  position: relative;
  top: 2px;
  height: 250px;
  width: 368px;
  overflow: auto;
  overflow-x: auto;
  background-color: rgb(0, 0, 0);
}

.Todo-edit-submit {
  position: relative;
  left: 15px;
}

.TurnOnBtn {
  position: relative;
  top: -19px;
  left: 191.5px;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  outline: none;
  opacity: 0;
}

/* Scrollbar */
body *::-webkit-scrollbar {
  width: 16px;
  height: 16px;
  background: none;
}

body *::-webkit-scrollbar-thumb,
body *::-webkit-scrollbar-button {
  width: 16px;
  height: 16px;
  background: silver;
  box-shadow: inset 1px 1px #dfdfdf, inset -1px -1px gray;
  border: 1px solid;
  border-color: silver #000 #000 silver;
}

body *::-webkit-scrollbar-track {
  image-rendering: optimizeSpeed;
  image-rendering: pixelated;
  image-rendering: optimize-contrast;
  background-image: url("data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHZpZXdCb3g9IjAgLTAuNSAyIDIiIHNoYXBlLXJlbmRlcmluZz0iY3Jpc3BFZGdlcyI+CjxtZXRhZGF0YT5NYWRlIHdpdGggUGl4ZWxzIHRvIFN2ZyBodHRwczovL2NvZGVwZW4uaW8vc2hzaGF3L3Blbi9YYnh2Tmo8L21ldGFkYXRhPgo8cGF0aCBzdHJva2U9IiNjMGMwYzAiIGQ9Ik0wIDBoMU0xIDFoMSIgLz4KPC9zdmc+");
  background-position: 0 0;
  background-repeat: repeat;
  background-size: 2px;
}

body *::-webkit-scrollbar-button {
  background-repeat: no-repeat;
  background-size: 16px;
}

body *::-webkit-scrollbar-button:single-button:vertical:decrement {
  background-image: url("data:image/svg+xml;base64,");
}

body *::-webkit-scrollbar-button:single-button:vertical:increment {
  background-image: url("data:image/svg+xml;base64,");
}

body *::-webkit-scrollbar-button:single-button:horizontal:decrement {
  background-image: url("data:image/svg+xml;base64,");
}

body *::-webkit-scrollbar-button:single-button:horizontal:increment {
  background-image: url("data:image/svg+xml;base64,");
}

body *::-webkit-scrollbar-corner {
  background: silver;
}
